*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  scroll-behavior: smooth;
}

body {
  box-sizing: border-box;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

a {
  display: inline-block;
  text-decoration: none;
}

ul, ol {
  list-style: none;
}

button {
  cursor: pointer;
  border: none;
  background-color: none;
}

input {
  display: inline-block;
  border: none;
}

img {
  display: inline-block;
  max-width: 100%;
}

/*
  FONT SIZES
  75px | 56px | 42px | 32px | 24px | 17px | 13px | 10px | 7.5px | 5.6px | 4.2px
*/
html {
  font-size: 50%;
}
@media (min-width: 75em) {
  html {
    font-size: 56.25%;
  }
}
@media (min-width: 112.5em) {
  html {
    font-size: 62.5%;
  }
}

body {
  font-size: 1.6rem;
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  line-height: 1.7;
  color: #6E7A89;
}

.heading-primary {
  font-size: 4.2rem;
  color: #FFFFFF;
}
@media (min-width: 56.25em) {
  .heading-primary {
    width: 85%;
    font-size: 5.6rem;
  }
}
@media (min-width: 75em) {
  .heading-primary {
    line-height: 1.5;
    font-size: 6.2rem;
  }
}

.heading-secondary {
  margin: 0 auto;
  margin-bottom: 1.6rem;
  width: 85%;
  font-size: 4rem;
  font-weight: 600;
  color: #132742;
  text-align: center;
  line-height: 1.2;
}
@media (min-width: 56.25em) {
  .heading-secondary {
    text-align: left;
    line-height: 1.5;
    width: 100%;
  }
}

.heading-tertiary {
  font-size: 2.4rem;
  color: #132742;
  margin-bottom: 1.2rem;
}

.button {
  padding: 1.4rem 2.4rem;
  border-radius: 10px;
  font-size: 1.6rem;
  font-weight: 600;
  font-family: inherit;
  text-align: center;
  cursor: pointer;
  transition: all 0.4s ease-out;
}

.button--outlined {
  background-color: transparent;
  color: #FFFFFF;
  font-weight: 400;
  border: 0.1px solid #FFFFFF;
}
.button--outlined:hover {
  font-weight: 600;
  background-color: #FFFFFF;
  color: #1A2222;
}

.button--white {
  background-color: #FFFFFF;
  border: 1px solid transparent;
}
.button--white:hover {
  background-color: #1A2222;
  color: #FFFFFF;
  border: 1px solid currentColor;
}

.button--orange {
  background-color: #CD412E;
  color: #FFFFFF;
  border: 1px solid transparent;
}
.button--orange:hover {
  background-color: transparent;
  border: 1px solid #CD412E;
  color: #CD412E;
}

.container {
  margin: 0 auto;
  width: 130rem;
  max-width: 95%;
}

.grid {
  display: grid;
  place-items: center;
}
.grid-2-cols {
  grid-template-columns: repeat(2, 1fr);
}
.grid-3-cols {
  grid-template-columns: repeat(3, 1fr);
}
.grid-4-cols {
  grid-template-columns: repeat(4, 1fr);
}
.grid-5-cols {
  grid-template-columns: repeat(5, 1fr);
}
.grid-3-gap {
  gap: 3rem;
}
.grid-1to2 {
  grid-template-columns: 1fr;
  gap: 3.2rem;
}
@media (min-width: 56.25em) {
  .grid-1to2 {
    grid-template-columns: repeat(2, 1fr);
    gap: 5.6rem;
  }
}
.grid-1to3 {
  grid-template-columns: 1fr;
  gap: 3.2rem;
}
@media (min-width: 56.25em) {
  .grid-1to3 {
    grid-template-columns: repeat(3, 1fr);
    gap: 4.8rem;
  }
}
.grid-2to3 {
  grid-template-columns: repeat(2, 1fr);
  gap: 3.2rem;
}
@media (min-width: 56.25em) {
  .grid-2to3 {
    grid-template-columns: repeat(3, 1fr);
    gap: 4.8rem;
  }
}
.grid-2to7 {
  grid-template-columns: repeat(2, 1fr);
  gap: 3.2rem;
}
@media (min-width: 56.25em) {
  .grid-2to7 {
    grid-template-columns: repeat(7, 1fr);
    gap: 1.2rem;
  }
}

.header {
  background-color: #1A2222;
  height: 7rem;
}
.header-fixed {
  width: 100%;
  position: fixed;
  z-index: 10;
  box-shadow: 3px 5px 5px rgba(0, 0, 0, 0.15);
}
@media (min-width: 56.25em) {
  .header {
    height: 8rem;
  }
}
.header__title {
  font-size: 2.4rem;
  font-weight: 600;
  color: #FFFFFF;
}
.header__container {
  height: 100%;
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
}
@media (min-width: 56.25em) {
  .header__container {
    gap: 4rem;
  }
}
.header__button {
  display: none;
}
@media (min-width: 56.25em) {
  .header__button {
    display: inline-block;
  }
}

.navigation {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  height: 100%;
  transform: translateX(100%);
  transition: all 0.4s ease-out;
  background-color: #1A2222;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-around;
}
@media (min-width: 56.25em) {
  .navigation {
    position: unset;
    transform: translateX(0%);
    flex-direction: row;
    align-items: center;
  }
}
.navigation__information {
  font-size: 1.2rem;
}
@media (min-width: 56.25em) {
  .navigation__information {
    display: none;
  }
}
.navigation__list {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 56.25em) {
  .navigation__list {
    flex-direction: row;
    gap: 6.4rem;
  }
}
.navigation__link {
  color: #FFFFFF;
  font-size: 2.4rem;
  font-weight: 600;
  margin-bottom: 5.6rem;
  border-bottom: 1px solid transparent;
  transition: all 0.4s ease-out;
}
.navigation__link:hover {
  border-bottom-color: #CD412E;
}
@media (min-width: 56.25em) {
  .navigation__link {
    font-size: 1.6rem;
    font-weight: 400;
    margin-bottom: 0;
  }
}
.navigation__open {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: center;
}
.navigation__close {
  width: 100%;
  padding-right: 2.4rem;
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: flex-end;
}
.navigation__open, .navigation__close {
  font-size: 4.8rem;
  color: #FFFFFF;
}
.navigation__open-icon, .navigation__close-icon {
  transition: all 0.4s ease-out;
  cursor: pointer;
}
.navigation__open-icon:hover, .navigation__close-icon:hover {
  color: #CD412E;
}
@media (min-width: 56.25em) {
  .navigation__open, .navigation__close {
    display: none;
  }
}

.show.navigation {
  transform: translateX(0%);
}

.hero {
  padding: 7rem 0;
  background-color: #1A2222;
  position: relative;
}
@media (min-width: 75em) {
  .hero {
    height: 75rem;
    margin-bottom: 38.4rem;
  }
}
.hero__container {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  gap: 3.2rem;
}
.hero__description {
  width: 80%;
  color: #FFFFFF;
  font-size: 1.8rem;
}
@media (min-width: 56.25em) {
  .hero__description {
    width: 60%;
  }
}
@media (min-width: 75em) {
  .hero__description {
    width: 40%;
  }
}
.hero__imagesBox {
  margin-bottom: 2rem;
}
@media (min-width: 112.5em) {
  .hero__imagesBox {
    margin-bottom: 9.6rem;
  }
}
.hero__image {
  margin: 0 auto;
}
.hero__image-box {
  width: 70%;
  overflow: hidden;
}
@media (min-width: 75em) {
  .hero__image-box {
    width: 100%;
  }
}
.hero__image-box img {
  border-radius: 9px;
}
.hero__image-box--1 {
  grid-column: 1/6;
}
@media (min-width: 75em) {
  .hero__image-box--1 {
    align-self: start;
    grid-column: 1/3;
  }
}
.hero__image-box--2 {
  display: none;
}
@media (min-width: 75em) {
  .hero__image-box--2 {
    display: unset;
    grid-column: 3/6;
  }
}
.hero__image-text {
  color: #FFFFFF;
  margin: 1.6rem 0;
}
@media (min-width: 112.5em) {
  .hero__image-text {
    width: 60%;
    text-align: left;
    font-size: 1.6rem;
    color: #6E7A89;
  }
}
.hero__item {
  position: absolute;
  width: 25rem;
}
@media (min-width: 56.25em) {
  .hero__item {
    width: 45rem;
  }
}
.hero__item--orange {
  top: 0;
  right: 0;
}
@media (min-width: 56.25em) {
  .hero__item--orange {
    width: 30rem;
    top: 5%;
    left: 0%;
  }
}
@media (min-width: 75em) {
  .hero__item--orange {
    top: 0%;
  }
}
@media (min-width: 112.5em) {
  .hero__item--orange {
    left: 20%;
  }
}
.hero__item--blue {
  top: 0;
  left: 0;
  z-index: -1;
}
@media (min-width: 56.25em) {
  .hero__item--blue {
    top: 50%;
  }
}
@media (min-width: 75em) {
  .hero__item--blue {
    top: 35%;
    z-index: unset;
  }
}
@media (min-width: 112.5em) {
  .hero__item--blue {
    top: 25%;
  }
}
.hero__item--purple {
  bottom: -10%;
  right: -3%;
}
@media (min-width: 56.25em) {
  .hero__item--purple {
    top: 5%;
  }
}

.u-display-none {
  display: none;
}

.u-text-center {
  text-align: center;
}

.u-margin-big {
  margin-bottom: 4.8rem;
}

.u-color-black {
  color: #000000;
}

.u-width-20rem {
  width: 20rem;
}

.u-width-100 {
  width: 100%;
}